@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
* {
    font-family: 'Montserrat', sans-serif;;
    box-sizing: border-box;
   }

   *:before,
   *:after {
    box-sizing: border-box;
   }

html,
body {
 height: 100%;
 position: relative;
 padding: 0;
 margin: 0;
}

.main-container {
    min-height: 100vh; /* will cover the 100% of viewport */
    overflow: hidden;
    display: block;
    position: relative;
    padding-bottom: 100px; /* height of your footer */
   }

.people-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minMax(250px, 250px));
    grid-gap: 0 2px;
    width: 75%;
    margin: 0 auto;
    justify-content: space-between;
}

.button {
    text-align: center;
    margin: 2px;
}
.button button {
    background-color: rgb(101, 186, 255);
    border: 1px solid black;
    border-radius: 5px;
    cursor: pointer;
    width: 250px;
    padding: 3px 6px;
    box-shadow: 0 3px 0 rgb(71, 74, 77);
    -webkit-transition: 0.1s;
    transition: 0.1s;
    font-size: 1.2rem;
    font-weight: 500;
}

.button button:hover {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    box-shadow: 0 6px 0 rgb(71, 74, 77);
}

.button button:active {
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
    box-shadow: 0 3px 0 rgb(71, 74, 77);
}

.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    color: #cacaca;
    font-weight: 600;
    display: flex;
    justify-content: space-between;
    text-align: center;
    background-color: rgb(36, 40, 46);
}

.darkmode {
    background-color: rgb(44, 57, 71);
}

.darkmode * {
    color: #cacaca;
}

.darkmode .button button{
    background-color: rgb(15, 15, 77);
}

.litemode {
    background-color: rgb(132, 239, 255);
}

.litemode h1 {
    color: black;
}

a {
    color: rgb(175, 76, 76) !important;
    text-decoration: none;
}

@media (max-width: 1200px) {
    .people-container {
        width: 100%;
    }
}

@media (max-width: 600px) {
    .people-container {
        justify-content: center;
    }
}
.stream-container {
    border: 1px solid black;
    border-radius: 10px;
    display: inline-block;
    width: 250px;
    height: 150px;
    overflow: hidden;
    margin: 10px;
    box-shadow: 0 5px 0 rgb(34, 37, 39);
    -webkit-transition: 0.15s;
    transition: 0.15s;
    margin: 10px auto;
}

.center {
    text-align: center;
}

.bold {
    font-weight: 600;
}

.hidden {
    display: none;
}

.hide {
    visibility: hidden;
}

.stream-container:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    box-shadow: 0 10px 0 rgb(75, 84, 94);
}

.stream-title {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 5px 7px;
}

.stream-info-container {
    display: flex;
    justify-content: space-between;
    padding: 0 20%;
}

.icon {
    width: 25px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.icon:hover {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
}

.user {
    font-size: 1.2rem;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    margin: 5px auto;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    text-decoration: none;
}

.user:hover {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
}

.Offline {
    opacity: 0.8;
}

.darkmode.Live {
    background-color: rgb(131, 21, 21);
}

.darkmode.Offline {
    background-color: rgb(15, 15, 77);
}

.litemode.Offline {
    background-color: rgb(101, 186, 255); 
}

.litemode.Live {
    background-color: rgb(255, 82, 82);
}

hr {
    border-color: black;
}
